
/* ======== HEADER ========*/

/* OVERALL HEADER */
#header {
	background-color: #333456;
	border-bottom: solid #eaeaea 2px;
	height: 100px;
	z-index: 1000;
}

/* LOGO IN HEADER */
#header-logo {
	vertical-align: middle;
	width: auto;
	height: 100%;
	margin: auto;
	display: block;
}

/* ======== STARS ========*/

/* EACH STAR */
.star {
	height: 100%;
	width: auto;
	color: #E552C8;
}

/* ROW OF STARS */
.starRow {
	display: inline-block;
	height: 22px;
	width: auto;
}

/* ======== DOLLAR SIGNS ========*/

.dollarSigns {
	display: inline;
	font-weight: 500;
}

/* ======== RESTAURANT LISTING ========*/

/* OVERALL RESTUARANT LISTING */
.RestaurantListing {
	height: 100px;
	background-color: white;
	border-bottom: solid #e0e0e0 5px;
	border-left: solid #e0e0e0 10px;
	border-right: solid #e0e0e0 10px;
}

/* ON HOVER OF RESTAURANT LISTING */
.RestaurantListing:hover { 
  cursor: pointer;
  background-color: #faedfc;
}

/* CONTAINER FOR LISTING IMAGE */
.RestaurantListing-photo-box {
	width: 30%;
	height: 100px;
	display: inline-block;
	background-repeat: no-repeat;
	background-position: center; 
	background-size: cover;
}

/* LISTING IMAGE */
.RestaurantListing-photo {
	width: 100%;
	height: auto;
}

/* CONTAINER FOR DETAILS */
.RestaurantListing-details-box {
	padding: 10px;
	width: 60%;
	margin-top: auto;
	margin-bottom: auto;
	vertical-align: top;
	display: inline-block;
}

/* DETAILS TITLE */
.RestaurantListing-details-title {
	margin: 0;
	width: 100%;
	display: inline-block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-family: 'Merriweather', serif;
	font-size: 16px;
	font-weight: 700;
}

/* DETAILS ADDRESS */
.RestaurantListing-details-address {
	margin: 0;
	width: 100%;
	display: inline-block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size: 13px;
}

/* DETAILS ICONS */
.RestaurantListing-details-icons {
	display: inline-block;
	overflow: visible;
	white-space: nowrap;
	width: 175px;
	font-size: 13px;
}

/* DETAILS NUMERIC RATING */
.RestaurantListing-details-rating {
	display: inline;
}

/* DETAILS TOTAL NUMBER OF RATINGS */
.RestaurantListing-details-totalRatings {
	display: inline;
	color: #a3a3a3;
}

/* RESTURANT CLOSED */
.RestaurantListing-details-closed {
	color: #a3a3a3;
	font-weight: 600;
	font-size: 13px;
}

/* RESTURANT OPEN */
.RestaurantListing-details-open {
	font-weight: 600;
	font-size: 13px;
}

/* CONTAINER FOR OPEN/CLOSED STATUS */
.RestaurantListing-details-openNow {
	margin: 0;
}

/* ======== MEDIA QUERIES FOR RESTAURANT LISTING ========*/

/* LANDSCAPE UP TO 700PX WIDE */
@media only screen and (max-width: 700px) and (orientation : landscape) {
	.RestaurantListing-details-totalRatings {
		display: none;
	}
}

/* LANDSCAPE UP TO 600PX WIDE */
@media only screen and (max-width: 600px) and (orientation : landscape) {
	.RestaurantListing-photo-box {
		display: none;
	}
	.RestaurantListing-details-totalRatings {
		display: inline;
	}
}

/* LANDSCAPE UP TO 500PX */
@media only screen and (max-width: 500px) and (orientation : landscape) {
	.RestaurantListing-details-totalRatings {
		display: none;
	}
}




/* ======== REVIEW ========*/

/* OVERALL REVIEW */
.review {
	background-color: white;
	margin: 0 10px 5px 10px;
	padding: 12px;
}

/* REVIEW AUTHOR */
.review-author {
	margin-top: 0;
	margin-bottom: 4px;
	font-weight: 600;
	font-size: 14px;
}

/* REVIEW COMMENT */
.review-comment {
	margin-bottom: 0;
	margin-top: 4px;
	font-size: 14px;
}

/* REVIEW RATING */
.review-rating {
	margin-right: 4px;
}

/* TIME OF REVIEW */
.review-time {
	margin-right: 4px;
	margin-left: 4px;
	font-size: 14px;
	color: #a3a3a3;
	font-weight: 500;
}

/* STARS FOR REVIEW */
.review .starRow {
	font-size: 14px;
}

/* ======== REVIEW PANEL ========*/

/* OVERALL REVIEW PANEL */
.reviewPanel {
	background-color: #e0e0e0;
	padding-top: 1px;
}

/* NO REVIEWS IMAGE */
.reviewPanel-noReviews {
	background-color: white;
	padding: 14px;
}


/* ======== NEW REVIEW ========*/

/* OVERALL NEW REVIEW COPMONENT */
.newReview {
	padding: 14px;
	background-color: white;
}

/* TEXT IN NEW REVIEW */
.newReview p {
	margin: 0 0 2px 0;
	font-size: 12px;
	font-weight: 500;
}

/* DIV HOLDING RADIO BUTTONS */
#newReview-ratingsDiv {
	margin-bottom: 10px;
}

/* RADIO BUTTONS */
#newReview-ratingsDiv input {
	margin-right: 14px;
	margin-left: 4px;
}

/* TITLE */
.newReview-title {
	font-size: 14px;
	margin: 0 0 14px 0;
}

/* COMMENT */
.newReview-comment {
	margin-bottom: 10px;
	resize: none;
	border: 1px solid #e3e3e3;
	border-radius: 2px;
	display: block;
	overflow: auto;
}

/* WHEN FOCUSED ON COMMENT */
.newReview-comment:focus {
	box-shadow: 0 0 4px 2px #526AB7;
	outline: none;
}

/* WHEN FOCUSED ON AUTHOR */
.newReview-author:focus {
	box-shadow: 0 0 4px 2px #526AB7;
	outline: none;
}

/* AUTHOR */
.newReview-author {
	margin-bottom: 10px;
	border: 1px solid #e3e3e3;
	border-radius: 2px;

}

/* CANCEL BUTTON */
.newReview-cancelButton {
	background: #e552c8;
	border: 2px solid #f4e6f4;
	border-radius: 24px;
	padding: 4px 10px;
	color: #ffffff;
	display: inline-block;
	font: normal bold 9pt "Open Sans", sans-serif;
	text-align: center;
	cursor: pointer;
	margin: 2px 0 0 0;
	outline: none;
}

/* SUBMIT BUTTON */
.newReview-submitButton {
	background: #e552c8;
	border: 2px solid #f4e6f4;
	border-radius: 24px;
	padding: 4px 10px;
	color: #ffffff;
	display: inline-block;
	font: normal bold 9pt "Open Sans", sans-serif;
	text-align: center;
	cursor: pointer;
	margin: 2px 4px 0 0;
	outline: none;
}



