/* ======== SIDE PANEL ========*/

/* OVERALL SIDE PANEL */
#SidePanel {
	height: 100vh;
	width: 40%;
	display: inline-block;
	background-color: #e3e3e3;
	overflow-x: hidden;
	overflow-y: scroll;
}

