	background: #e552c8;
	border: 2px solid #f4e6f4;
	border-radius: 24px;
	padding: 4px 10px;
	color: #ffffff;
	display: block;
	font: normal bold 9pt "Open Sans", sans-serif;
	text-align: center;
	cursor: pointer;
	margin: auto;
	outline: none;
}

