/* ======== MAIN PANEL ========*/

/* MAP */
#mainPanel-map {
	height: 100vh;
	width: 60%;
	display: inline-block;
}

/* LOADING IMAGE */
.mainPanel-loadingImg {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1000;
	width: 100%;
	height: 104vh;
	background-image: url(/restaurant-review-site/static/media/loadingImg2.d4848dc5.jpg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

/* ======== MEDIA QUERIES FOR SIDE PANEL ========*/

/* LANDSCAPE UP TO 800PX */
@media only screen and (orientation: landscape) and (max-width: 800px) {
	#mainPanel-map {
		width: 55%;
	}
}

/* SHOW FAKE SHADOW BESIDE MAP ON LARGER THAN 701PX WIDE ONLY */
@media only screen and (orientation: landscape) {
	#mainPanel-map:before {
	    box-shadow: -4px 0 10px -5px rgba(0, 0, 0, 0.6) inset;
	    content: "";
	    height: 100%;
	    right: 0;
	    position: absolute;
	    width: 5px;
	    z-index: 1000;
	}
}

/* WHEN SCREEN BECOMES A VERTICAL PROPORTION */
@media only screen and (orientation : portrait) and (max-width: 700px) {
	#mainPanel-map {
		width: 100%;
		display: block;
		height: 40vh;
	}

	#mainPanel-map.hideMap {
		display: none;
	}

	#mainPanel-map:after {
	    box-shadow: -4px 0 11px -6px rgba(0, 0, 0, 1) inset;
	    content: "";
	    width: 100%;
	    bottom: 0;
	    position: absolute;
	    height: 5px;
	    z-index: 1000;
	    display: block;
	}
}





/* ======== GLOBAL STYLES ========*/

/* OVERALL BODY */
body {
	margin: 0;
	overflow: hidden;
	font-family: 'Montserrat', sans-serif;
	font-size: 10pt;
	font-weight: 400;
}

/* REMOVE DEFAULT OUTLINE ON FOCUS */
*:focus {
	outline: none;
}
